    function addToast(type, msg, timeout){
        let nt = {
            id: page.luid(),
            type,
            message: msg,
            visible: false,
            expiry: timeout || 5000
        };
        toast.push(nt);
        window.setTimeout(()=>{
            nt.visible = true;
            let o = document.querySelector("#toast-box-" + nt.id);
            if(o){
                o.className = "transition transition-100 toast-box " + toastConfig[nt.type].style;
            }
        }, 10);
        if(nt.expiry >= 0){
            window.setTimeout(()=>{
            burnToast(nt);
            }, nt.expiry);
        }
        m.redraw();
        return nt.id;
    }

