                                    thumb = m("img", {style: "width: 100%; aspect-ratio: 1; object-fit: cover; border-radius: 8px; cursor: pointer;", src: imgSrc + "/256x256", onclick: function(e){ e.preventDefault(); previewSrc = imgSrc + "/512x512"; m.redraw(); }});
