        DESERT: "\u{1F3DC}",      // Desert
        DUNES: "\u{1F3DC}",       // Desert dunes
        FOREST: "\u{1F332}",      // Tree
        GLACIER: "\u{1F9CA}",     // Ice
        GRASS: "\u{1F33F}",       // Herb/grass
        HILL: "\u{26F0}",         // Mountain
        HILLS: "\u{26F0}",        // Mountains
        JUNGLE: "\u{1F334}",      // Palm
        LAKE: "\u{1F4A7}",        // Water
