    tetris.component = {
        keyHandler : keyControl,
        scoreCard : getScoreCard,

        oninit : function(x){
            init();
        },
        oncreate : function (x) {

        },
        onremove : function(x){
            endGame();
        },

        view: function () {
            return modelPanel();
         }
    };

    page.components.tetris = tetris.component;
