                                m("button", {
                                    class: "flyout-button text-center ml-2",
                                    ondragover: (e) => e.preventDefault(),
                                    ondrop: handleTrashDrop
                                }, [m("span", { class: "material-symbols-outlined material-icons-24" }, "delete"), "Trash"])
