    /// EXPORT
    /// ========================================

    let tableEntry = {
        editEntry: editEntry,
        deleteEntry: deleteEntry,
        newEntry: newEntry,
        checkEntry: checkEntry,
        cancelEntry: cancelEntry
    };

    /// Register with page.components
    if (typeof page !== "undefined" && page.components) {
        page.components.tableEntry = tableEntry;
    }

