                    let fixed = jsonStr;

                    // Step 0: Strip JS-style comments
                    fixed = fixed.replace(/\/\/[^\n]*/g, '');
                    fixed = fixed.replace(/\/\*[\s\S]*?\*\//g, '');

                    // Step 1: Protect existing double-quoted strings from regex modification
