                    name: 'Pass 1: Neutral baseline',
                    state: {
                        biometric: { emotion: 'neutral', age: 30, gender: 'unknown' },
                        labels: [],
                        recentVoiceLines: [],
                        currentText: '',
                        audio: { sweepStart: 8, sweepTrough: 2, sweepEnd: 8, isochronicEnabled: false },
                        visuals: { mode: 'single', effects: ['particles'] },
                        elapsedMinutes: 0
                    }
